p {
  color: black;
}
.scratch-card {
  width: 100%;

  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.scratch-card-img-div {
  width: 40%;
}
.scratch-card-img {
  height: 80%;
}
.scratch-card-text {
  width: 60%;
}
.scratch-card-0 {
  background: white;
  border-radius: 6px;
  display: flex;

  color: black;
  font-size: 16px;
  box-shadow: 10px 10px 8px #888888;
  flex-direction: column;
}

/* TYPEFACE */
.scratch-card-from-txt {
  font-size: 1.2rem;
  margin-left: 30px;
  margin-top: 20px;
  font-family: "Cabin";
  opacity: 0.9;
  align-self: flex-start;
}
.scratch-card-text {
  align-content: flex-start;
  display: flex;
  padding-left: 10px;
  flex-direction: column;
}
.scratch-card-locked-txt {
  font-family: "Cabin";
  color: #3c4fb0;
  font-weight: bold;
  font-size: 1.5rem;
  align-self: flex-start;
}

.scratch-card-locked-desc {
  font-family: "Cabin";
  font-weight: bold;
  font-size: 1.1rem;
  align-self: flex-start;
}
.scratch-card-locked-btn {
  border-radius: 3px;
  font-family: "Varela";
  text-decoration: none;
  color: white;

  font-weight: normal;
  text-align: center;
  padding: 6px 0;
  font-size: 1.2rem;
  background: #0d7cff;
}
.scratch-card-locked-btn:hover {
  font-weight: bolder;
  color: white;
}
.locked-by-div {
  display: flex;

  flex-direction: row;

  justify-content: flex-end;
}
.locked-by-txt {
  margin-top: 5px;
  margin-right: 5px;
  font-family: "Cabin";
  font-size: 1.1rem;
}
.locked-by-img {
  width: 30px;
  height: 40px;
}
@media only screen and (max-width: 600px) {
  .scratch-card {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    align-items: flex-start;
  }
  .scratch-card-img-div {
    width: 40%;
  }
  .scratch-card-img {
    width: 80%;
  }
  .scratch-card-0 {
    width: 95%;
  }
  .scratch-card-locked-desc {
    font-family: "Cabin";
    font-weight: bold;
    font-size: 1.1rem;
    text-align: left;
  }
  .scratch-card-locked-btn {
    margin: 3px 6px;
  }
}
