.container div {
  margin: 10px auto;
  width: 80%;
}
#cardImg {
  width: 100%;
  margin: 10px auto;
}
.card {
  /* Add shadows to create the "card" effect */
  width: sm-;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
}

/* On mouse-over, add a deeper shadow */
.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

/* Add some padding inside the card container */

.mother-div2 {
  background-color: #f4f3f3;
  padding: 20px;

  margin: 0 auto;
}
.mother-div3 {
  justify-content: center;
  display: flex;
  margin: 0 auto;
}
.section-1 {
  width: 75%;
}
.section-2 {
  background: #e2dcdc;
  width: 20%;
}
.scratch-img {
  width: 200px;
}

@media only screen and (max-width: 600px) {
  .mother-div2 {
    padding: 5px;
  }
  .mother-div3 {
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
  }
  .section-1 {
    width: 100%;
  }
  .section-2 {
    background: #e2dcdc;
    width: 90%;
  }
  .container div {
    margin: 10px auto;
    width: 100%;
  }
}
.modal-buttons-div {
  display: flex;
  margin-top: 20px;
  justify-content: space-between;
}
.modal-buttons-close {
  min-width: 100px;
  margin-left: 10px;
  min-height: 35px;
  background-color: #4f4f4f;
  border-radius: 6px;
  border: none;
  font-family: "Varela";
  color: white;
}
.modal-buttons-accept {
  min-width: 100px;
  margin-right: 10px;
  min-height: 35px;
  background-color: #ff5101;
  border-radius: 6px;
  border: none;
  font-family: "Varela";
  color: white;
}

/* TYPEFACE */
.unlock-txt {
  font-family: "Cabin";
  font-size: 1.4rem;
  margin-top: 25px;
  font-weight: bold;
}
.unlock-txt-desc {
  font-family: "Cabin";
  text-align: left;
  margin-left: 20px;
  align-self: flex-start;
  font-size: 1.3rem;
  margin-top: 20px;
}
.unlock-txt-desc-detail {
  font-family: "Cabin";
  text-align: center;
  align-self: flex-start;
  font-size: 1.1rem;
  margin-top: 20px;
  color: #4f4f4f;
}

.from-txt {
  color: #b4afaf;
  font-weight: bolder;
}
.modal-title {
  font-family: "Open Sans";
  font-size: 1.3rem;
}
/* MODAL TIMER */

#clockdiv {
  font-family: sans-serif;
  color: #fff;
  display: inline-block;
  font-weight: 100;
  text-align: center;
  font-size: 20px;
}

#clockdiv > div {
  padding: 10px;
  margin: 10px;
  border-radius: 3px;
  background: #565252;
  display: inline-block;
}

#clockdiv div > span {
  padding: 10px;
  border-radius: 3px;
  background: #ff5101;
  display: inline-block;
}

.smalltext {
  padding-top: 5px;
  font-size: 16px;
}
#createTxt {
  color: black;
  font-size: 20px;
  font-family: "Open Sans";
  font-weight: bolder;
}
@media only screen and (max-width: 600px) {
  #clockdiv {
    font-family: sans-serif;
    color: #fff;
    display: inline-block;
    font-weight: 100;
    text-align: center;
    font-size: 15px;
  }

  #clockdiv > div {
    padding: 5px 10px;
    margin: 5px;
    border-radius: 3px;
    background: #565252;
    display: inline-block;
  }

  #clockdiv div > span {
    padding: 5px 10px;
    border-radius: 3px;

    background: #ff5101;
    display: inline-block;
  }
  .smalltext {
    padding-top: 5px;
    font-size: 14px;
  }
  #createTxt {
    color: black;
    font-size: 16px;
    font-family: "Open Sans";
    font-weight: bold;
  }
}

/* MODAL TIMER ENDS */
