/* LAYOUT */
.mother {
  font-family: "Open Sans", sans-serif;
  margin: 0 auto;
  font-size: 16px;
  display: flex;
  flex-direction: column;
}
.section-one {
  padding-bottom: 40px;
  background-color: #ff5c00;
}
.columns {
  display: flex;
  width: 90%;
  margin: 0 auto;
  flex-direction: row;
  height: 60%;
  justify-content: space-evenly;
}

.c1 {
  width: 55%;

  display: flex;
  flex-direction: column;
}
.c1-r1 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.c2 {
  width: 40%;
  margin-top: 60px;
}
.create-btn {
  color: #ff5c00;
  border: none;
  padding-top: 6px;
  padding-bottom: 6px;
  padding-left: 20px;
  padding-right: 20px;
  text-decoration: none;
  font-size: 1.3rem;
  border-radius: 0.4rem;
  background-color: white;
}
.create-btn:hover {
  color: green;
}
.r1 {
  display: flex;
  flex-direction: row;
}
/* Section two */
.section-two {
  padding-bottom: 30px;
  padding-top: 30px;
  background-color: #f4f3f3;
}

.rows div {
  width: 80%;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.card-icon {
  color: rgb(233, 159, 22);
  width: 30%;
  align-self: center;
}

.rows div div {
  padding: 20px;
  width: 400px;
  display: flex;
  display: flex;
  flex-direction: column;
}

.section-three {
  background-color: #ff5c00;
}

.footer-row {
  display: flex;
  margin: auto 0;
  flex-direction: row;
}
.footer-row-col {
  width: 50%;
}
.feedback-box {
  margin: 40px 20px;
}
.footer-img {
  max-width: 90%;
  max-height: 80%;
}
.section-four {
  background: white;
  padding-bottom: 30px;
}
.from-logo {
  width: 10%;
}

/* MEDIA */
@media only screen and (max-width: 600px) {
  .columns {
    display: flex;
    width: 100%;
    margin: 0 auto;
    flex-direction: column-reverse;
    align-items: center;
  }
  .c1-r1 {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .vertical {
    display: none;
  }
  .rows div {
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }

  .footer-img {
    display: none;
  }
  .footer-row {
    display: flex;
    margin: auto 0;
    flex-direction: column;
  }
  .footer-row-col {
    width: 100%;
  }
  .c1 {
    width: 85%;
  }
  .c2 {
    width: 85%;
  }
  .rows div div {
    padding: 10px;
    width: 95%;
  }
  .from-logo {
    width: 40%;
  }
}

/* TYPEFACE */

h1 {
  font-size: 2rem;
  color: white;
  text-align: left;
  font-family: "Libre Baskerville";
}
h2 {
  font-size: 1.8rem;
  color: black;
  text-align: center;
  font-family: "Libre Baskerville";
}
h3 {
  font-family: "Cabin";
  color: white;
}

span {
  font-size: 1rem;
}
.top-txt {
  margin-top: 50px;
}
.sec-txt {
  text-align: left;
  margin: 30px 10px;
  font-family: "Cabin";
  color: white;
  font-size: 1.6rem;
}
.vertical {
  border-left: 2px solid white;
  height: 3rem;
  margin-left: 20px;
  margin-right: 20px;
}

.watch-video {
  margin-top: 6px;
  font-family: "Cabin";
}
.read-more-txt {
  color: #0d7cff;
  font-family: "Open Sans";
}
.card-title {
  color: #ff5c00;
  margin-top: 6px;
  font-family: "Cabin";
  font-size: 1.3rem;
}
.card-desc {
  font-family: "Open Sans";
  color: black;
}
.section-two-topic-line {
  width: 15%;
  color: #ff5c00;
  background-color: #ff5c00;
  margin: 0 auto;
}
.h2-white {
  color: white;
  text-align: center;
}
.from-txt {
  color: #b4afaf;
  font-weight: bolder;
}
@media only screen and (max-width: 600px) {
  .top-txt {
    text-align: center;
  }
  .sec-txt {
    text-align: center;
  }
}
