.feed-form {
  width: 60%;
  text-align: center;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}
#emailHelp {
  text-align: left;
  margin-left: 10px;
  margin-top: 10px;
  color: white;
}
.inputId {
  background: white;
  margin-top: 10px;
  color: black;
  box-shadow: white;
  padding: 10px;
  font-family: "Open Sans";
  min-height: 50px;
  border-radius: 4px;
}
#submitId {
  min-height: 50px;
  width: 25%;
  margin-top: 10px;
  background: white;
  font-family: "Cabin";
  border: white solid 2px;
  font-size: 1.4rem;
  font-weight: bold;
  color: #ff5c00;
  border-radius: 6px;
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: black;
  opacity: 0.8; /* Firefox */
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: black;
  opacity: 0.8; /* Firefox */
}

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: black;
  opacity: 0.8; /* Firefox */
}
@media only screen and (max-width: 600px) {
  #submitId {
    min-height: 50px;
    width: 35%;
  }
  .feed-form {
    width: 80%;
  }
  ::placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: black;
    font-size: 1.1rem;
    opacity: 0.8; /* Firefox */
  }

  :-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: black;
    font-size: 1.1rem;
    opacity: 0.8; /* Firefox */
  }

  ::-ms-input-placeholder {
    /* Microsoft Edge */
    color: black;
    font-size: 1.1rem;
    opacity: 0.8; /* Firefox */
  }
}
