/* LAYOUT */
.mother-div {
  font-family: "Open Sans", sans-serif;
  margin: 0 auto;
  font-size: 16px;
  color: black;
  background: #ff5c00;
  display: flex;
}
.section-four {
  background-color: #e2dcdc;
}
.second-div {
  width: 90%;
  margin: 30px;
  background: white;
  border-radius: 3px;
  display: flex;
  flex-direction: row;
  width: 100%;
}

.c11 {
  width: 60%;
  margin: 0 0 60px 0;
  padding: 20px;
  border-radius: 6px 0px 0px 6px;
  background-color: white;
}
.c22 {
  background-color: #e2dcdc;
  width: 40%;
  border-radius: 0px 6px 6px 0px;
  padding: 20px;
}
.create-head {
  display: flex;

  margin: 10px;
  justify-content: center;
}
.create-head-img {
  width: 60px;
  margin: 10px;
  height: 60px;
}
.msg-txt {
  width: 100%;
}
.create-column {
  display: flex;
  width: 100%;

  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.create-btn {
  background: #0d7cff;
  width: 70%;
  min-height: 50px;
  color: white;
  font-family: "Varela";
  font-size: 1.2rem;
}
.create-btn:hover {
  color: white;
  font-weight: bold;
}
.checkbox-lyt {
  width: 70%;
  text-align: center;
  margin: 10px auto;
}
.form-lyt {
  width: 70%;
  text-align: center;
  margin: 10px 0;
}
.checkbox-box {
  width: 20px;
  height: 20px;
}

/* FEEDBACK */
.feedback-card {
  background: white;
  width: 70%;
  padding: 10px;

  border-radius: 6px;
  margin: 10px auto;
  display: flex;
  flex-direction: row;
}
.feedback-img {
  width: 80%;
}
.feedback-img-div {
  width: 10%;
}
.feedback-name {
  font-family: "Cabin";
  font-weight: bolder;
  font-size: 1.1rem;
}
.feedback-feedback {
  font-family: "Open Sans";
}

.feedback-body {
  width: 90%;
  margin-left: 10px;
  text-align: left;
}
.from-logo {
  width: 20%;
}
.from-txt {
  color: #b4afaf;
  font-weight: bolder;
}

/* TYPEFACE----------------------------------------------------------------------------- */
#create-txt {
  color: #ff5c00;
  font-weight: bold;
  font-family: "Open Sans";
}
.create-head-txt {
  margin: auto 0;
  font-weight: bolder;
  font-size: 1.3rem;
}
.msg-txt {
  min-height: 100px;
  width: 70%;
  font-size: 1.2rem;
  font-weight: bolder;
  color: blue;
  margin: 10px 0;
  padding: 10px;
}
.name-txt {
  width: 70%;
  margin: 10px 0;
  font-size: 1.1rem;
  font-weight: bolder;
  color: black;
  padding: 10px;
}
.email-txt {
  width: 70%;
  margin: 10px 0 0 0;
  font-size: 1.1rem;
  font-weight: bolder;
  color: black;
  padding: 10px;
}
.small-txt {
  color: black;
  opacity: 0.8;
  font-weight: bolder;
  font-size: 1.1rem;
  margin-left: 10px;
}
.left-txt {
  margin: 10px auto;
  width: 70%;
}

/* FEEDBACK */
.feedback-head-txt {
  margin-top: 20px;
}
.many-more-txt {
  color: #929292;
  font-family: "Cabin";
  font-weight: bold;
  font-size: 1.2rem;
}
.modal-title {
  font-size: 1.2rem;
  text-align: center;
  margin: 0 auto;
  align-self: center;

  font-family: "Varela";
  color: black;
  padding: 10px;
}
.modal-desc {
  font-size: 1rem;
  text-align: center;
  margin: 0 auto;
  font-family: "Cabin";
  color: black;
  padding: 10px;
}

/* PLACEHOLDER */

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: black;
  font-weight: bold;
  font-size: 1.2rem;
  font-family: "Open Sans";
  opacity: 0.8; /* Firefox */
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: black;
  font-weight: bold;
  font-size: 1.2rem;
  font-family: "Open Sans";
  opacity: 0.8; /* Firefox */
}

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: black;
  font-weight: bold;
  font-size: 1.2rem;
  font-family: "Open Sans";
  opacity: 0.8; /* Firefox */
}

@media only screen and (max-width: 600px) {
  .second-div {
    width: 100%;
    margin: 15px;
    background: white;
    border-radius: 3px;
    display: flex;
    flex-direction: column;
  }
  .c11 {
    width: 100%;
    margin: 0 0 30px 0;
    padding: 10px;

    border-radius: 6px 6px 0px 0px;
    background-color: white;
  }
  .c22 {
    background-color: #e2dcdc;
    width: 100%;
    border-radius: 0px 0px 6px 6px;
    padding: 10px;
  }
  .feedback-card {
    background: white;
    width: 80%;
    padding: 10px;

    border-radius: 6px;
    margin: 10px auto;
    display: flex;
    flex-direction: row;
  }
  .form-lyt {
    width: 100%;
    text-align: center;
    margin: 10px 0;
  }
  .small-txt2 {
    font-weight: bolder;
    font-size: 0.8rem;
    margin-left: 3px;
  }
  .create-head-txt {
    margin: auto 0;
    font-weight: bolder;
    font-size: 1rem;
  }
  ::placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: black;
    font-weight: bold;
    font-size: 1rem;
    font-family: "Open Sans";
    opacity: 0.8; /* Firefox */
  }

  :-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: black;
    font-weight: bold;
    font-size: 1rem;
    font-family: "Open Sans";
    opacity: 0.8; /* Firefox */
  }

  ::-ms-input-placeholder {
    /* Microsoft Edge */
    color: black;
    font-weight: bold;
    font-size: 1rem;
    font-family: "Open Sans";
    opacity: 0.8; /* Firefox */
  }
  .checkbox-lyt {
    width: 90%;
    text-align: center;
    margin: 6px auto;
  }
  #emailId {
    width: 90%;
  }
  #fromId {
    width: 90%;
  }
  #msgId {
    width: 90%;
  }
  .create-btn {
    width: 88%;
  }
  .left-txt {
    margin: 6px auto;
    width: 90%;
  }
  .create-head-txt {
    width: 90%;
  }
}
