.snake-comp {
  pointer-events: none;
}
/* TYPEFACE */

.game-title-txt {
  font-family: "Open Sans";
  font-size: 1.2rem;
  font-weight: bold;
  margin-top: 10px;
}
.mother-container {
  display: flex;

  width: 100%;
  background-color: #ffc418;
  flex-direction: column;
  border-radius: 6px;
}
.container1 {
  display: flex;
  padding: 20px;

  width: 100%;
}
.vert-btns {
  display: flex;
  margin: auto 0;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.horiz-btns {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.btn-img {
  width: 60px;
}
.section-11 {
  width: 70%;
  pointer-events: none;
}
.section-22 {
  width: 30%;
  margin: auto;
}
.btn-left {
  margin-right: 10px;
}
.btn-right {
  margin-left: 10px;
}
.btn-top {
  margin-left: 10px;
}
@media only screen and (max-width: 600px) {
  .container1 {
    display: flex;
    flex-direction: column;
    padding: 20px;

    width: 100%;
  }
  .section-11 {
    width: 100%;
    height: 40%;
    pointer-events: none;
  }
  .section-22 {
    width: 70%;
    height: 50%;
  }
  .btn-top {
    margin-left: 0px;
  }
  .btn-left {
    margin-right: 0px;
  }
  .btn-right {
    margin-left: 0px;
  }
  .game-title-txt {
    font-family: "Open Sans";
    font-size: 1rem;
    font-weight: bold;
    margin-top: 10px;
  }
}
